
.com-footer {
  height: 40px;
  line-height: 40px;
  background: #fff;
  border-top: 1px solid #e0e0e0;
  /*<!--box-shadow: 0px -1px 1px #e0e0e0;-->*/
}
.com-footer ul {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    /*border-top: 1px solid #ccc;*/
}
.com-footer ul li {
      -webkit-box-flex: 1;
          -ms-flex: 1;
              flex: 1;
      text-align: center;
}
.com-footer ul li a {
        text-decoration: none;
        color: #5e5e5e;
        display: block;
        /*font-size: .22rem;*/
}
.com-footer ul li a.router-link-active {
          color: #f44e50 !important;
}



