
.jijin-list-tabs {
  /*section tr.bg td{background:#fffbf4;}*/
  /*add by zw*/
}
.jijin-list-tabs section {
    margin-top: .2rem;
    background: #fff;
}
.jijin-list-tabs section table {
    width: 100%;
    font-size: .3rem !important;
}
.jijin-list-tabs section th, .jijin-list-tabs section td {
    text-align: center;
    padding: .1rem 0;
}
.jijin-list-tabs section th.tl, .jijin-list-tabs section td.tl {
    text-align: left;
    padding-left: .2rem;
}
.jijin-list-tabs section th {
    height: .64rem;
    color: #999;
    font-weight: 400;
}
.jijin-list-tabs section .tl span {
    display: block;
    color: #f8b551;
}
.jijin-list-tabs section .up {
    color: #f44e51;
}
.jijin-list-tabs section .down {
    color: #5fd82a;
}
.jijin-list-tabs tr {
    vertical-align: top !important;
}
.jijin-list-tabs table tr:nth-of-type(odd) {
    background: #fff;
}
.jijin-list-tabs table tr:nth-of-type(even) {
    background: #fffbf4;
}



